/*blocks*/
.container {
  display: flex;
  justify-content: space-between;
  max-width: 1200px;
  margin: 0 auto;
}
.container.container--portfolio {
  flex-direction: column;
  justify-content: flex-start;
  max-width: 1200px;
  margin: 0 auto;
}
.container.container--portfolio h2 {
  font-size: 30px;
  line-height: 42px;
  font-weight: 700;
  font-family: "Lato", "Arial", sans-serif;
  margin: 0 0 50px 0;
}
@media (max-width: 1280px) {
  .container {
    max-width: 964px;
  }
  .container.container--portfolio {
    max-width: 964px;
  }
}
@media (max-width: 1023px) {
  .container {
    max-width: 708px;
    flex-direction: column;
    align-items: center;
  }
  .container.container--portfolio {
    max-width: 708px;
    flex-direction: column;
    align-items: flex-start;
  }
  .container.container--hello {
    flex-direction: column-reverse;
  }
}

.container__info {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  min-width: 353px;
  width: 353px;
  margin: 0 30px 0 0;
}
.container__info svg {
  width: 150px;
  height: 134px;
}
.container__info h2 {
  font-size: 30px;
  line-height: 42px;
  font-weight: 700;
  font-family: "Lato", "Arial", sans-serif;
  text-align: left;
  margin: 0;
}
.container__info p {
  margin: 10px 0 20px 0;
  font-size: 16px;
  line-height: 24px;
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 400;
}
@media (max-width: 1280px) {
  .container__info {
    min-width: 270px;
    width: 270px;
  }
}
@media (max-width: 1023px) {
  .container__info {
    min-width: auto;
    width: 100%;
    margin: 0;
    box-sizing: border-box;
  }
  .container__info svg {
    display: none;
  }
}

.container__list {
  display: flex;
  flex-wrap: wrap;
  column-gap: 30px;
  width: 100%;
}
.container__list.container__list--experience {
  flex-direction: column;
}
@media (max-width: 1023px) {
  .container__list {
    flex-direction: column;
  }
}

.container__list-element {
  width: 100%;
  max-width: calc(50% - 15px);
  padding: 0 0 30px 0;
  box-sizing: border-box;
  border-bottom: solid 1px #e3e3e3;
}
.container__list-element h3 {
  font-size: 18px;
  line-height: 30px;
  font-weight: 700;
  font-family: "Lato", "Arial", sans-serif;
  margin: 0;
}
.container__list-element p:first-of-type {
  font-size: 16px;
  line-height: 24px;
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 400;
  margin: 0;
}
.container__list-element p:last-of-type {
  font-size: 16px;
  line-height: 24px;
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 400;
  font-style: italic;
  margin: 15px 0 0 0;
}
.container__list-element.container__list-element--experience {
  width: 100%;
  max-width: 100%;
  padding: 0 0 30px 0;
  box-sizing: border-box;
  border-bottom: solid 1px #e3e3e3;
}
.container__list-element.container__list-element--experience:nth-child(n+2) {
  margin: 20px 0 0 0;
  padding: 0 0 30px 0;
  box-sizing: border-box;
  border-bottom: solid 1px #e3e3e3;
}
.container__list-element.container__list-element--experience:last-of-type {
  padding: 0;
  border-bottom: none;
}
@media (max-width: 1023px) {
  .container__list-element {
    max-width: 100%;
  }
  .container__list-element.container__list-element:nth-child(n+2) {
    margin: 20px 0 0 0;
    padding: 0 0 30px 0;
    box-sizing: border-box;
    border-bottom: solid 1px #e3e3e3;
  }
  .container__list-element.container__list-element:last-of-type {
    padding: 0;
    border-bottom: none;
  }
}

.container__list-element:nth-child(n+3) {
  margin: 20px 0 0 0;
  padding: 0;
  border-bottom: none;
}

.container__portfolio {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  row-gap: 20px;
}
@media (max-width: 1280px) {
  .container__portfolio {
    justify-content: space-between;
  }
}

.container__portfolio-element {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin: 0 40px 40px 0;
}
.container__portfolio-element picture {
  overflow: hidden;
}
.container__portfolio-element img {
  width: 270px;
  height: 270px;
  transition: all 0.3s ease-in-out;
}
.container__portfolio-element p {
  font-weight: 700;
  font-family: "Lato", "Arial", sans-serif;
  font-size: 18px;
  line-height: 30px;
  color: #252525;
  margin: 15px 0 0 0;
  transition: all 0.3s ease;
}
.container__portfolio-element p:last-of-type {
  font-size: 16px;
  line-height: 24px;
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 400;
  margin: 0;
}
.container__portfolio-element:hover {
  cursor: pointer;
}
.container__portfolio-element:hover img {
  transform: scale(120%) rotate(5deg);
}
.container__portfolio-element:hover p {
  color: #3333ff;
  text-decoration: underline;
  text-decoration-color: #3333ff;
}
.container__portfolio-element:hover p:last-of-type {
  color: #252525;
  text-decoration: none;
}
@media (max-width: 1280px) {
  .container__portfolio-element {
    margin: 0 0 40px 0;
  }
}
@media (max-width: 1023px) {
  .container__portfolio-element img {
    width: 340px;
    height: 340px;
  }
}
@media (max-width: 767px) {
  .container__portfolio-element {
    width: calc(50% - 20px);
  }
  .container__portfolio-element img {
    width: 100%;
    height: 100%;
  }
}
@media (max-width: 500px) {
  .container__portfolio-element {
    width: 100%;
  }
  .container__portfolio-element img {
    width: 100%;
    height: 100%;
  }
}

.container__portfolio-element:nth-child(4n+4) {
  margin: 0 0 20px 0;
}

.education {
  width: 100%;
  border-bottom: solid 1px #e3e3e3;
  padding: 40px 30px;
  box-sizing: border-box;
}

.hello {
  width: 100%;
  border-bottom: solid 1px #e3e3e3;
  padding: 40px 30px;
  box-sizing: border-box;
}

.logo {
  display: flex;
  align-items: center;
  font-size: 24px;
  line-height: 36px;
  font-family: "Lato", "Arial", sans-serif;
  font-weight: 700;
}
.logo picture {
  height: 64px;
  width: 64px;
  margin: 0 10px 0 0;
}
.logo img {
  margin: 0 10px 0 0;
}

.menu {
  display: flex;
}
.menu.menu--mob {
  display: none;
}
@media (max-width: 1023px) {
  .menu.menu--desktop {
    display: none;
  }
  .menu.menu--opened {
    display: flex;
    position: absolute;
    width: auto;
    height: auto;
    z-index: 15;
    top: 50vh;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .menu.menu--opened a {
    color: #fff;
  }
}

.menu__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style-type: none;
  padding: 0;
}
.menu__list li {
  margin: 0 25px 0 0;
}
.menu__list a {
  text-transform: uppercase;
  font-size: 14px;
  font-family: "Lato", "Arial", sans-serif;
  font-weight: 700;
  line-height: 23px;
  color: #000;
  transition: all 0.3s ease;
}
.menu__list a:hover {
  color: #3333ff;
  text-decoration: underline;
  text-decoration-color: #3333ff;
}
.menu__list li:last-of-type {
  margin: 0;
}
@media (max-width: 1023px) {
  .menu__list {
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
  }
  .menu__list li {
    margin: 0 0 30px 0;
  }
  .menu__list a:hover {
    color: #fff;
    text-decoration: none;
  }
}

.page-footer {
  width: 100%;
  height: 100%;
  padding: 40px 30px;
  box-sizing: border-box;
}

.page-header {
  width: 100%;
  padding: 20px 30px;
  box-sizing: border-box;
  border-bottom: solid 1px #e3e3e3;
}

.page-header__container {
  position: relative;
  max-width: 1200px;
  height: auto;
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
}
@media (max-width: 1280px) {
  .page-header__container {
    max-width: 964px;
  }
}
@media (max-width: 1023px) {
  .page-header__container {
    max-width: 708px;
  }
}

.page-header__container-menu-toggle {
  display: none;
}
@media (max-width: 1023px) {
  .page-header__container-menu-toggle {
    display: block;
    position: absolute;
    top: 50%;
    right: 0;
    width: 20px;
    height: 15px;
    display: flex;
    align-items: center;
    margin: 0;
    padding: 0;
    border: none;
    outline: none;
    cursor: pointer;
    z-index: 10;
  }
  .page-header__container-menu-toggle.page-header__container-menu-toggle--opened {
    z-index: 30;
    position: fixed;
    top: 60px;
    right: 30px;
  }
  .page-header__container-menu-toggle.page-header__container-menu-toggle--opened::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 20px;
    height: 1px;
    background: rgb(255, 255, 255);
    transform: rotate(45deg);
    box-shadow: none;
  }
  .page-header__container-menu-toggle.page-header__container-menu-toggle--opened::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 20px;
    height: 1px;
    background: rgb(255, 255, 255);
    transform: rotate(-45deg);
  }
  .page-header__container-menu-toggle:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #3333ff;
    box-shadow: 0 6px 0 0 #3333ff, 0 12px 0 0 #3333ff;
  }
}

@media (max-width: 1023px) {
  .page-main__overlay.page-main__overlay--opened {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background-color: #3333ff;
    z-index: 10;
  }
}

.page {
  position: relative;
  display: grid;
  grid-template-rows: auto 1fr auto;
  min-height: 100vh;
  width: 100%;
  margin: 0;
  padding: 0;
  color: #252525;
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
}

.portfolio {
  width: 100%;
  border-bottom: solid 1px #e3e3e3;
  padding: 40px 30px;
  box-sizing: border-box;
}
@media (max-width: 1023px) {
  .portfolio {
    padding: 40px 30px 0;
  }
}

.skills {
  width: 100%;
  border-bottom: solid 1px #e3e3e3;
  padding: 40px 30px;
  box-sizing: border-box;
}

.info {
  display: flex;
  flex-direction: column;
  max-width: 50%;
  margin: 0 30px 0 0;
  box-sizing: border-box;
}
.info h1 {
  margin: 0;
  font-size: 36px;
  line-height: 45px;
  font-weight: 700;
  font-family: "Lato", "Arial", sans-serif;
}
.info p {
  margin: 10px 0 20px 0;
  font-size: 16px;
  line-height: 24px;
  font-family: "Roboto", "Arial", sans-serif;
}
@media (max-width: 1023px) {
  .info {
    max-width: 100%;
    margin: 0;
  }
}

.info__contacts {
  display: flex;
  flex-direction: column;
  margin: 0 0 30px 0;
  box-sizing: border-box;
}

.info__contacts-container {
  display: flex;
  align-items: center;
  margin: 0 0 20px 0;
}
.info__contacts-container p {
  margin: 0;
}
.info__contacts-container a {
  color: #252525;
  transition: all 0.3s ease;
}
.info__contacts-container a:hover {
  color: #3333ff;
}
.info__contacts-container .info__contacts-info {
  font-size: 14px;
  line-height: 24px;
}

.info__contacts-container:last-of-type {
  margin: 0;
}

.info__contacts-social {
  height: 24px;
  display: flex;
  list-style-type: none;
  padding: 0;
  margin: 0;
}
.info__contacts-social li {
  margin: 0 15px 0 0;
}
.info__contacts-social li:last-of-type {
  margin: 0;
}
.info__contacts-social svg {
  height: 20px;
  width: 20px;
  transition: all 0.3s ease;
}
.info__contacts-social svg:hover {
  fill: #3333ff;
}

.info__contacts-title {
  min-width: 100px;
  font-size: 12px;
  font-weight: 700;
  text-transform: uppercase;
  font-family: "Lato", "Arial", sans-serif;
  line-height: 20px;
}

.info__buttons-links {
  display: flex;
  align-items: center;
}
.info__buttons-links a {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 170px;
  height: 50px;
  box-sizing: border-box;
  padding: 0 30px;
  color: #fff;
  font-size: 14px;
  line-height: 22px;
  text-transform: uppercase;
  font-weight: 700;
  font-family: "Lato", "Arial", sans-serif;
  letter-spacing: 0.3px;
  transition: all 0.3s ease;
}
.info__buttons-links a:first-of-type {
  background-color: #3333ff;
  margin: 0 30px 0 0;
}
.info__buttons-links a:first-of-type:hover {
  box-shadow: 10px 10px 20px rgba(51, 51, 255, 0.2);
}
.info__buttons-links a:last-of-type {
  color: #252525;
  background-color: #efefef;
}
.info__buttons-links a:last-of-type:hover {
  box-shadow: 10px 10px 20px rgba(37, 37, 37, 0.2);
}
@media (max-width: 500px) {
  .info__buttons-links {
    flex-direction: column;
  }
  .info__buttons-links a {
    width: 100%;
  }
  .info__buttons-links a:first-of-type {
    margin: 0 0 20px 0;
  }
}

@media (max-width: 1280px) {
  .photo img {
    width: 455px;
    height: 255px;
  }
}
@media (max-width: 1023px) {
  .photo {
    margin: 0 0 20px 0;
  }
  .photo img {
    width: 710px;
    height: 410px;
  }
}
@media (max-width: 767px) {
  .photo {
    width: 100%;
    height: auto;
    max-height: 410px;
  }
  .photo img {
    width: 100%;
    height: 100%;
  }
}

.contacts-form {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
}
.contacts-form button {
  width: 170px;
  height: 50px;
  align-self: flex-end;
  outline: none;
  border: none;
  background-color: #3333ff;
  color: #fff;
  font-size: 12px;
  line-height: 20px;
  text-transform: uppercase;
  font-weight: 700;
  font-family: "Lato", "Arial", sans-serif;
  cursor: pointer;
  transition: all 0.3s ease;
}
.contacts-form button:hover {
  box-shadow: 10px 10px 20px rgba(51, 51, 255, 0.2);
}

.contacts-form__wrapper {
  display: flex;
  margin: 0 0 30px 0;
}
.contacts-form__wrapper textarea {
  width: 100%;
  max-height: 160px;
  box-sizing: border-box;
  padding: 15px;
  resize: none;
  outline: none;
  border: solid 2px #e3e3e3;
  border-radius: 2px;
  box-sizing: border-box;
  color: #252525;
  font-size: 14px;
  font-family: "Lato", "Arial", sans-serif;
  line-height: 20px;
  font-weight: 700;
  transition: all 0.3s ease;
}
.contacts-form__wrapper textarea:hover {
  border: solid 2px #3333ff;
}
.contacts-form__wrapper textarea::placeholder {
  font-size: 14px;
  font-family: "Lato", "Arial", sans-serif;
  line-height: 20px;
}
@media (max-width: 767px) {
  .contacts-form__wrapper {
    flex-direction: column;
  }
}

.contacts-form__wrapper-name {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 0 30px 0 0;
}
.contacts-form__wrapper-name label {
  display: none;
}
.contacts-form__wrapper-name input {
  width: 270px;
  height: 60px;
  outline: none;
  border: solid 2px #e3e3e3;
  border-radius: 2px;
  box-sizing: border-box;
  padding: 15px;
  font-size: 14px;
  font-family: "Lato", "Arial", sans-serif;
  line-height: 20px;
  color: #252525;
  font-weight: 700;
  transition: all 0.3s ease;
}
.contacts-form__wrapper-name input:hover {
  border: solid 2px #3333ff;
}
@media (max-width: 767px) {
  .contacts-form__wrapper-name {
    margin: 0 0 20px;
  }
  .contacts-form__wrapper-name input {
    width: 100%;
  }
  .contacts-form__wrapper-name input:first-of-type {
    margin: 0 0 20px 0;
  }
}
.contacts-form__wrapper-name input::placeholder {
  font-size: 14px;
  font-family: "Lato", "Arial", sans-serif;
  line-height: 20px;
  font-weight: 700;
}