.container {
  display: flex;
  justify-content: space-between;
  max-width: 1200px;
  margin: 0 auto;

  &.container--portfolio {
    flex-direction: column;
    justify-content: flex-start;
    max-width: 1200px;
    margin:0 auto;

    h2 {
      font-size: 30px;
      line-height: 42px;
      font-weight: 700;
      font-family: $lato;
      margin: 0 0 50px 0;
    }
  }

  @media (max-width: $desktop-width) {
    max-width: 964px;

    &.container--portfolio {
      max-width: 964px;
    }
  }
  @media (max-width: $tablet-width) {
    max-width: 708px;
    flex-direction: column;
    align-items: center;

    &.container--portfolio {
      max-width: 708px;
      flex-direction: column;
      align-items: flex-start;
    }

    &.container--hello {
      flex-direction: column-reverse;
    }
  }
}

.container__info {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  min-width: 353px;
  width: 353px;
  margin: 0 30px 0 0;

  svg {
    width: 150px;
    height: 134px;
  }

  h2 {
    font-size: 30px;
    line-height: 42px;
    font-weight: 700;
    font-family: $lato;
    text-align: left;
    margin: 0;
  }

  p {
    margin: 10px 0 20px 0;
    font-size: 16px;
    line-height: 24px;
    font-family: $roboto;
    font-weight: 400;
  }

  @media (max-width: $desktop-width) {
    min-width: 270px;
    width: 270px;
  }

  @media (max-width: $tablet-width) {
    min-width: auto;
    width: 100%;
    margin: 0;
    box-sizing: border-box;

    svg {
      display: none;
    }
  }
}

.container__list {
  display: flex;
  flex-wrap: wrap;
  column-gap: 30px;
  width: 100%;

  &.container__list--experience {
    flex-direction: column;
  }

  @media (max-width: $tablet-width) {
    flex-direction: column;
  }
}

.container__list-element {
  width: 100%;
  max-width: calc(50% - 15px);
  padding: 0 0 30px 0;
  box-sizing: border-box;
  border-bottom: solid 1px $border;

  h3 {
    font-size: 18px;
    line-height: 30px;
    font-weight: 700;
    font-family: $lato;
    margin: 0;
  }

  p:first-of-type {
    font-size: 16px;
    line-height: 24px;
    font-family: $roboto;
    font-weight: 400;
    margin: 0;
  }

  p:last-of-type {
    font-size: 16px;
    line-height: 24px;
    font-family: $roboto;
    font-weight: 400;
    font-style: italic;
    margin: 15px 0 0 0;
  }

  &.container__list-element--experience {
    width: 100%;
    max-width: 100%;
    padding: 0 0 30px 0;
    box-sizing: border-box;
    border-bottom: solid 1px $border;
  }

  &.container__list-element--experience:nth-child(n+2) {
    margin: 20px 0 0 0;
    padding: 0 0 30px 0;
    box-sizing: border-box;
    border-bottom: solid 1px $border;
  }

  &.container__list-element--experience:last-of-type {
    padding: 0;
    border-bottom: none;
  }

  @media (max-width: $tablet-width) {
    max-width: 100%;

    &.container__list-element:nth-child(n+2) {
      margin: 20px 0 0 0;
      padding: 0 0 30px 0;
      box-sizing: border-box;
      border-bottom: solid 1px $border;
    }

    &.container__list-element:last-of-type {
      padding: 0;
      border-bottom: none;
    }
  }
}

.container__list-element:nth-child(n+3) {
  margin: 20px 0 0 0;
  padding: 0;
  border-bottom: none;
}

.container__portfolio {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  row-gap: 20px;

  @media (max-width: $desktop-width) {
    justify-content: space-between;
  }
}

.container__portfolio-element {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin: 0 40px 40px 0;

  picture {
    overflow: hidden;
  }

  img {
    width: 270px;
    height: 270px;
    transition: all .3s ease-in-out;
  }

  p {
    font-weight: 700;
    font-family: $lato;
    font-size: 18px;
    line-height: 30px;
    color: $text-dark;
    margin: 15px 0 0 0;
    transition: all .3s ease;
  }

  p:last-of-type {
    font-size: 16px;
    line-height: 24px;
    font-family: $roboto;
    font-weight: 400;
    margin: 0;
  }

  &:hover {
    cursor: pointer;

    img {
      transform: scale(120%) rotate(5deg);
    }

    p {
      color: $main;
      text-decoration: underline;
      text-decoration-color: $main;
    }

    p:last-of-type {
      color: $text-dark;
      text-decoration: none;
    }
  }

  @media (max-width:$desktop-width) {
    margin: 0 0 40px 0;
  }

  @media (max-width: $tablet-width) {
    img {
      width: 340px;
      height: 340px;
    }
  }

  @media (max-width:$mobile-width-only) {
    width: calc(50% - 20px);

    img {
      width: 100%;
      height: 100%;
    }
  }

  @media (max-width:$mobile-width-medium) {
    width: 100%;

    img {
      width: 100%;
      height: 100%;
    }
  }
}

.container__portfolio-element:nth-child(4n+4) {
  margin: 0 0 20px 0;
}
