.photo {
  @media (max-width: $desktop-width) {
    img {
      width: 455px;
      height: 255px;
    }
  }

  @media (max-width:$tablet-width) {
    margin: 0 0 20px 0;

    img {
      width: 710px;
      height: 410px;
    }
  }

  @media (max-width:$mobile-width-only) {
    width: 100%;
    height: auto;
    max-height: 410px;

    img {
      width: 100%;
      height: 100%;
    }
  }
}
